import React from "react"
import styled from "styled-components"
import breakpoint from "styled-components-breakpoint"

const ContactInfoWrapper = styled.div`
  display: flex;
  flex-flow: column;
  align-items: center;
  background-color: ${props => props.theme.color.lo};
  width: 18rem;
  padding: 2rem;
  text-align: center;
  margin: .25rem;
  font-size: .9rem;
  ${props => breakpoint('sm', props.theme.breakpoints)`
    width: 22rem;
  `};
`
const Line = styled.h6`
  margin: .85rem;
  white-space: pre-line;
`
const Address = Line.extend`
`
const Phone = Line.extend`

`
const Author = Line.extend`
  max-width: 8rem;
  border-top: 2px solid ${props => props.theme.color.p};
  padding: .5rem;
`
const AuthorLink = styled.a`
  text-decoration: none;
  color: ${props => props.theme.color.p};
`
const Legal = Line.extend`

`
const Email = Line.extend`

`

const ContactInfo = ({contact, author, legal})=>(
  <ContactInfoWrapper>
    <Address>{contact.address}</Address>
    <Phone>{contact.phone}</Phone>
    <Email>{contact.email}</Email>
    <Legal>{legal}</Legal>
    <Author href={author.link}>Website by <br/><AuthorLink href={author.link}>{author.name}</AuthorLink></Author>
  </ContactInfoWrapper>
)

export default ContactInfo;



