import React from 'react'
import Link from 'gatsby-link'
import styled from 'styled-components'
import breakpoint from 'styled-components-breakpoint'

import ContactForm from '../components/ContactForm.js'
import {Button, PhotoWrapper, PageHeader} from '../utils/styled-components.js'
import RegistrationBar from '../components/RegistrationBar.js'
import ContactInfo from "../components/ContactInfo.js"

import {translate} from "react-i18next"
//styles


const PhotoWrapperContact = PhotoWrapper.extend`
  background-image:   linear-gradient(rgba(0,0,0,0.45), rgba(0,0,0,0.45)),
  url('http://res.cloudinary.com/ds0f4qyyt/image/upload/s--Apiy0pSL--/c_scale,q_jpegmini,w_3773/v1493834434/london_f0prw6.jpg');
`

const Label = styled.label`

`
const ContactWrapper = styled.div`
  display: flex;
  flex-flow: column;
  align-items: center;
`
const AlternateLink = styled.div`
  display: flex;
  flex-flow: column;
  padding: .5rem 1rem;
  border-top: 2px solid ${props => props.theme.color.p};
  border-bottom: 2px solid ${props => props.theme.color.p};
  margin-bottom: 2rem;
  ${props => breakpoint('md', props.theme.breakpoints)`
    flex-flow: row;
  `};
  width: 18rem;
  ${props => breakpoint('sm', props.theme.breakpoints)`
    width: 22rem;
  `};
`
const AlternateLinkLabel = styled.label`
  font-size: 1.125rem;
  color: ${props => props.theme.color.l};
  padding: .5rem;
  align-self: center;
`

const ContactPage = ({data, t}) => (
  <ContactWrapper>
    <PhotoWrapperContact/>
    <PageHeader>{t('contact.header')}</PageHeader>
    <h4
    dangerouslySetInnerHTML={{__html: t('contact.subheader')}}
    style={{textAlign: 'center', color: "white", lineHeight: "1.2rem"}}>
    </h4>
    <ContactForm contactEmail={data.site.siteMetadata.contact.email} />
    <AlternateLink>
    <AlternateLinkLabel htmlFor={"register"}>{t('contact.register')}</AlternateLinkLabel>
    <Link
    style={{alignSelf: "center"}}
    id="register"
    to="/registration/"><Button>{t('contact.registerButton')}</Button></Link>
    </AlternateLink>
    <ContactInfo
      contact={data.site.siteMetadata.contact}
      author={data.site.siteMetadata.author}
      legal={data.site.siteMetadata.legal}
      />

  </ContactWrapper>
)
export const query = graphql`
  query ContactQuery{
    site{
      siteMetadata{
        contact{
          email
          address
          phone
        }
        author{
          name
          link
        }
        legal
      }
    }
  }
`

export default translate()(ContactPage)



