import React from 'react'
import styled from "styled-components"
import breakpoint from "styled-components-breakpoint"
import Link from 'gatsby-link'

import {Button} from '../utils/styled-components.js'
import { translate } from "react-i18next"

const Wrapper = styled.div`
  width: 100%;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 999;
  background-color: ${props => props.theme.color.lo};
  ${props => breakpoint('sm', props.theme.breakpoints)`
    position: fixed;
  `};
`
const Bar = styled.nav`
  margin: 0 auto;
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-width: 300px;
  max-width: 400px;
  padding: .25rem;
`
const RegButton = Button.extend`
  width: 8rem;
  ${props => breakpoint('sm', props.theme.breakpoints)`
    width: 16rem;
  `}
`

const RegistrationBar = ({t})=>(
  <Wrapper>
    <Bar>
      <Link to={'/registration/'}><RegButton>{t('register.patient')}</RegButton></Link>
      <Link to={'/contact/'}><RegButton>{t('register.organization')}</RegButton></Link>
    </Bar>
  </Wrapper>
)

export default translate()(RegistrationBar);



